body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background: #f9f9f9;
}

.logo {
  height: 50px;
  padding: 10px;
}
.navbar {
  display: flex;
  flex-direction: row;
}

.board {
  display: flex;
  height: 100vh;
  flex-direction: column;
}

.header {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
  height: 10%;
}

.he-l {
  width: 90%;
  display: flex;
  align-items: center;
  font-size: 20px;
  font-weight: 600;
}
.title-1 {
  font-size: 20px;
  font-weight: 600;
  color: #898989;
  margin-top: 15px;
  margin-bottom: 15px;
}

.title-1 {
  font-size: 20px;
  font-weight: 600;
  color: #898989;
}
.task-title {
  background-color: #7453b8;
  height: 40px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}

.he-r {
  display: flex;
  align-items: center;
  justify-content: center;
}

.add-btn {
  background-color: #6039ac;
  color: white;
  border-width: 0;
  margin: auto;
  padding: 5px;
  width: 150px;
  border-radius: 5px;
  margin: 20px;
}

.grid {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.grid-task {
  /* background: rgb(175, 175, 175); */
  width: 22%;
  height: 100vh;
}

.card {
  font-size: large;
  margin-top: 10px;
  width: 100%;
  height: auto;
  border-radius: 5px;
  padding: 15px;
  box-shadow: 10px 7px 36px -4px rgba(184, 184, 184, 1);
}

.info {
  font-size: small;
  color: #898989;
}

.icon {
  display: flex;
  justify-content: flex-end;
  padding: 5px;
  margin-top: 20px;

}


.login{
  background-color: #7453b8;
  display: flex;
  width: 350px;
  height: 400px;
  flex-direction: column;
  padding: 20px;
  border-radius: 20px;
  
}

.text{
  border-radius: 10px;
  width: 300px;
  padding: 10px;
  border-width: 0;
  
}

.label{
  color: white;
  font-size: large;
}

.input{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}


.btn-login{
margin-top: 30px;
background-color: white;
width: 50%;
height: 40px;
border-radius: 10px;
border-width: 0;
}


.login-box{
  margin-top: 40px;
  }

  .scroll{
    overflow: scroll;
    height: 500px;
  }
